@import url(https://fonts.googleapis.com/css2?family=Crimson+Text:ital,wght@0,400;1,700&family=Italianno&family=Raleway:ital,wght@0,200;0,300;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* 
font-family: 'Crimson Text', serif;
font-family: 'Italianno', cursive;
font-family: 'Raleway', sans-serif; */
.header {
    height: 600px;
    background-color: #2b2b2c;
   
}
.header img {
    height: 560px;
    margin: 0 auto;
    display: block;
}
.HeaderText {
    margin-top: 236px;
    margin-left: 200px;
}
.HeaderText h1 {
    color: aqua;
    /* font-family: 'Crimson Text', serif; */
    font-weight: bold;
    letter-spacing: 1px;
    font-family: cursive;      
}
.HeaderText h4{
    color:rgb(223, 247, 247)
}
.HeaderText p {
    color: #71d279;
    font-family: 'Italianno', 'cursive';
    font-size: 30px;
}

.fa-star{
    color: goldenrod;
    font-size: 25px;
}
.project-header {
    text-transform: uppercase;
    font-size: 39px;
    line-height: 65px;
    font-family: cursive;
    color: coral;
    letter-spacing: 4px;
    text-shadow: 3px 0px 0px #000000;
}
.footer{
    height: 60px;
    background-color: beige;
    color: cadetblue;
    padding: 10px 0px;
    text-align: center;
}
